@import "./index.css";
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Favorit Pro";
    font-weight: 350;
    line-height: 1.1;
}
@font-face {
    font-family: "Favorit Pro";
    src: url("../fonts/FavoritPro/FavoritPro-Regular.otf") format("OpenType");
}
.audience {
    background: #ebd8d1;
    width: 100vw;
    height: 100vh;
    margin: auto;
    position: relative;
}
.tag {
    width: 10rem;
    height: 10rem;
    transition: all 0.2s ease-in-out;
    border-radius: 50%;
    &.selected {
        filter: brightness(1.2);
        border: 4px #fff solid;
        transition: all 0.2s ease-in-out;
    }
    &s_container {
        max-width: 1070px;
        margin: auto;
    }
}
.main-content {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.interactive {
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 150px;
    right: 0;
    margin: auto;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(4px);
    animation: appearance 0.3s ease-in;
}
.karaoke-text p{
    line-height: 1.4;
}
.violet {
    padding: 2px 4px;
    background: linear-gradient(0deg, #bb95f1, #bb95f1), #81f0f6;
    padding: 0px 8px;
    border-radius: 10px;
    cursor: pointer;
    &:hover{
        filter: grayscale(.8);
    }
}
button.block {
    background: linear-gradient(0deg, #bb95f1, #bb95f1), #81f0f6;
    transition: all 0.25s ease-in-out;
    color: #fff;
    margin-top: 2rem;
    font-size: 42px;
    padding: 30px 88px;
    border-radius: 25px;
    &:disabled {
        filter: grayscale(1);
    }

    &:hover {
        transition: all 0.25s ease-in-out;
        background: linear-gradient(0deg, #579aea, #579aea), #81f0f6;
    }
    &:active {
        transition: all 0.25s ease-in-out;
        background: linear-gradient(0deg, #6aa2e6, #6aa2e6), #81f0f6;
    }
}

$mainBg: linear-gradient(27.68deg, #ffc500 -37.23%, #f28f99 104.74%), #a4f375;
.question_block {
    background: $mainBg;
    color: #fff;
    text-align: center;
    border-radius: 25px;
    max-width: 1070px;
    font-size: 40px;
}
.audience-container {
    max-width: 1220px;
    margin: auto;
    width: 100%;
    padding-bottom: 63%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    .audience-inner {
        position: absolute;
        background-size: cover !important;
        left: 0;
        right: 0;
        top: 0;
        width: 100%;
        height: 0;
        padding-bottom: 71.1%;
        bottom: 0;
        margin: auto;
    }
}
.start-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 0;
    top: 0;
    height: fit-content;
    max-width: 1070px;
    > div {
        grid-area: 2 / 2;
        color: #fff;
        font-size: 1.5rem;
    }
}
h2 {
    font-size: 55px;
    line-height: 65px;
    margin-bottom: 1.5rem;
}
p {
    margin-bottom: 2.5rem;
}
@keyframes appearance {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
